@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body,
#root {
  height: 100%;
}

/* Markdown Content Styles */
.markdown-content {
  @apply text-gray-800 leading-relaxed;
}

/* Headings */
.markdown-content h1 {
  @apply text-4xl font-bold mt-8 mb-4;
}

.markdown-content h2 {
  @apply text-3xl font-semibold mt-6 mb-3;
}

.markdown-content h3 {
  @apply text-2xl font-medium mt-5 mb-2;
}

.markdown-content h4 {
  @apply text-xl font-medium mt-4 mb-2;
}

.markdown-content h5 {
  @apply text-lg font-medium mt-3 mb-1;
}

.markdown-content h6 {
  @apply text-base font-medium mt-3 mb-1;
}

/* Paragraphs and lists */
.markdown-content p,
.markdown-content ul,
.markdown-content ol {
  @apply mb-4;
}

.markdown-content p:last-child {
  @apply mb-0;
}

.markdown-content ul,
.markdown-content ol {
  @apply pl-8;
}

.markdown-content ul {
  @apply list-disc;
}

.markdown-content ol {
  @apply list-decimal;
}

.markdown-content li {
  @apply mb-1;
}

/* Links */
.markdown-content a {
  @apply text-blue-600 hover:text-blue-800 underline;
}

/* Blockquotes */
.markdown-content blockquote {
  @apply pl-4 border-l-4 border-gray-300 italic my-4;
}

/* Code blocks */
.markdown-content pre {
  @apply bg-gray-100 rounded p-4 overflow-x-auto mb-4;
}

.markdown-content code {
  @apply bg-gray-100 rounded px-1 py-0.5 font-mono text-sm;
}

/* Horizontal rule */
.markdown-content hr {
  @apply my-8 border-t border-gray-300;
}

/* Tables */
.markdown-content table {
  @apply w-full border-collapse mb-4;
}

.markdown-content th,
.markdown-content td {
  @apply border border-gray-300 px-4 py-2;
}

.markdown-content th {
  @apply bg-gray-100 font-semibold;
}
